import React from 'react'
import { Link } from "gatsby"
import styled from "styled-components"

import posed from 'react-pose';

const List = posed.div({
  visible: { y: 0, opacity: 1, staggerChildren: 0, transition: { duration: 0}},
  hidden: { y: 0, opacity: 1, transition: { duration: 0}}
});

const ListElem = posed.div({
  visible: { x: 0, opacity: 1, transition: { duration: 90}},
  hidden: { x: 50, opacity: 0, transition: { duration: 90}}
});


const StyledThing = styled.li`

  background-color: white;
  list-style: none;
  margin: 0;
  padding: 5px 15px;
  white-space: nowrap;
  border-radius: 100px;
  font-family: 'Calibre-Regular';
  font-size: 24px;

  @media(min-width: 992px) {
    background-color: white;
    list-style: none;
    margin: 0;
    padding: 10px 15px;
    white-space: nowrap;
    border-radius: 100px;
    font-family: 'Calibre-Regular';
    font-size: 34px;
  }

`

const StyledLink = styled(Link)`
position: relative;
text-decoration: none;
color: blue;
top: 4px;


@media(min-width: 992px) {
  top: 5px;
}
`

class StyledLiEven extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            isVisible: false
        }

        this.mouseEnter = this.mouseEnter.bind(this);
        this.mouseLeave = this.mouseLeave.bind(this);
    }

    componentDidMount(){
        if(this.props.animated === false) {
          this.setState({
            isVisible: true
          });
        }
      }

    mouseEnter(){
        console.log('hello');
        this.setState({
          isVisible: true
        });
      }
    
      mouseLeave(){
        this.setState({
          isVisible: false
        });

        if(this.props.animated === false) {
          this.setState({
            isVisible: true
          });
        }
      }

    render(){
        const {isVisible} = this.state;
                let counter = 0;

        return(
            <>
            <StyledThing onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}>
                <StyledLink to={this.props.item ? `/${this.props.item.slug}/` : ''}>{this.props.item.name}</StyledLink>
            </StyledThing>
        <List  className ="test" pose={this.state.isVisible ? 'visible' : 'hidden'} style={{position: 'relative', top: '10px', width: 0, whiteSpace: 'nowrap', backgroundColor: 'white', color: 'black'}}>

          {this.props.item.tags.map((item, index) => {
             counter ++;
            if (counter % 2 === 0) {
              return (
                <ListElem style={{margin:'20px 0 0 0'}}>
                <span style={{backgroundColor: 'white', padding: '8px 10px 0px 10px', borderRadius: '100px'}}>
                  {item}
                </span>
              </ListElem>
              )
            } else {
              return (
                <ListElem style={{margin:'20px 0 0 0'}}>
                <span style={{backgroundColor: 'white', padding: '8px 10px 0px 10px'}}>
                  {item}
                </span>
              </ListElem>
              )
            }
          })
        }
          </List>
        </>
        )
    }
}

export default StyledLiEven;
